.footer {
	color: $white;
	border-bottom: 10px solid $black;
	background-color: $limedSpruce;
	&__inner {
		margin-top: 80px;
		padding-bottom: 27px; }
	&__bottom {
		margin-top: 70px; }
	&__logo {
		max-width: 57px;
		display: block;
		img {
			max-width: 100%;
			display: block; } } }

.contacts {
	font-family: $lato;
	font-size: 15px;
	line-height: 1.3;
	font-weight: 400;
	&__col {
		vertical-align: top;
		display: inline-block;
		&:not(:first-child) {
			margin-left: 55px; } }
	&__title {
		margin-bottom: 12px;
		font-family: $montserrat;
		font-size: 16px;
		font-weight: 700; }
	a {
		color: inherit;
		border-bottom: 1px solid transparent;
		transition: border-color .3s;
		@include hover {
			border-color: currentColor; } } }

.ready {
	padding: 100px 0;
	background-color: $brightSun;
	&__subtitle {
		color: $dark;
		font-family: $montserrat;
		font-size: 20px;
		font-weight: 300;
		text-transform: uppercase; }
	&__title {
		max-width: 490px; } }
