.works {
	position: relative;
	z-index: 1;
	&:before {
		content: '';
		position: absolute;
		top: -77px;
		left: -86px;
		border-radius: 50%;
		width: 171px;
		height: 171px;
		background-image: linear-gradient(-10deg, rgba(255,245,187,0.00) 5%, #FFE886 77%);
		@include sm {
			top: -21px;
			left: -82px;
			width: 114px;
			height: 114px; } }
	&__col {
		width: 50%;
		float: left;
		@include sm {
			float: none; } }
	&__content {
		padding: 90px 73px 75px 0;
		@include md {
			width: 70%; }
		@include sm {
			padding: 35px 0;
			width: 100%; } }
	&__slider {
		padding: 85px 0 0;
		@include md {
			width: 30%; }
		@include sm {
			padding-top: 0;
			width: 100%; } }
	&__slider-in {
		margin: 0 -27px -62px;
		@include md {
			margin: 0 -27px -98px; }
		@include xs {
			margin-left: -20px;
			margin-right: -20px; } }
	&__title {
		margin-bottom: 28px;
		@include sm {
			margin: 0 8px 25px 13px; } }
	&__description {
		max-width: 512px; }
	&__range {
		margin-top: 35px;
		max-width: 512px;
		@include sm {
			display: none; } } }
